.tournament-widget-score {
  /* position: absolute; */
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  -ms-flex-direction: row;
      flex-direction: row;
  /* overflow: hidden; */
}

.tournament-widget-score__rank {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  width: 100px;
  height: 100%;
  position: relative;
}

.tournament-widget-score__team {
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  height: 100%;
  width: 80%;
}

.tournament-widget-score__team-image-container {
  height: 46px;
  width: 46px;
  margin: 0;
  position: relative;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.tournament-widget-score__team-member--avatar {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-top-left-radius: 5px;
}
.tournament-widget-score__team-member--avatar.avatar-2:nth-child(1) {
  -webkit-clip-path: polygon(0 0, 0% 100%, 100% 0);
          clip-path: polygon(0 0, 0% 100%, 100% 0);
}
.tournament-widget-score__team-member--avatar.avatar-2:nth-child(2) {
  -webkit-clip-path: polygon(100% 100%, 0% 100%, 100% 0);
          clip-path: polygon(100% 100%, 0% 100%, 100% 0);
}

.tournament-widget-score__team-name-container {
  margin: auto;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  padding: 5px;
  width: calc(100% - 46px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.tournament-widget-score__team-member--name {
  line-height: 1;
  margin: auto 0;
  font-size: 20px;
  font-weight: 700;
  opacity: 0.7;
  overflow: hidden;
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.tournament-widget-score__count {
  height: 100%;
  width: 20%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  font-size: 30px;
  font-weight: 800;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  position: relative;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.tournament-widget-score__count.score-increased {
  background: rgba(166, 66, 255, 0.1);
}

.tournament-widget-score__count--value {
  margin: auto;
  -webkit-transform: scale(0.8);
      -ms-transform: scale(0.8);
          transform: scale(0.8);
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
  display: block;
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
}

.tournament-widget-score__count.score-increased .tournament-widget-score__count--value {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.tournament-widget-score__avatar--img {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  height: 50%;
  width: 100%;
}

.tournament-widget-score__count--floating-number {
  font-size: 28px;
  font-weight: 800;
  color: #a642ff;
  position: absolute;
  text-shadow: rgba(166, 66, 255, 0.5) 0px 0px 20px;
  top: 50%;
  z-index: 1000;
  left: 50%;
  text-align: center;
  width: 100px;
  height: 100px;
  margin-left: -50px;
  margin-top: -50px;
  -webkit-animation: float-up 1s ease forwards;
          animation: float-up 1s ease forwards;
}

.tournament-widget-score__count--floating-number.animate {
  -webkit-animation: float-up 800ms ease-in-out forwards;
          animation: float-up 800ms ease-in-out forwards;
}

@-webkit-keyframes float-up {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  20% {
    opacity: 1;
    -webkit-transform: translate3d(0, -10px, 0);
            transform: translate3d(0, -10px, 0);
  }
  800% {
    -webkit-transform: translate3d(0, -50px, 0);
            transform: translate3d(0, -50px, 0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate3d(0, -80px, 0);
            transform: translate3d(0, -80px, 0);
    opacity: 0;
  }
}

@keyframes float-up {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  20% {
    opacity: 1;
    -webkit-transform: translate3d(0, -10px, 0);
            transform: translate3d(0, -10px, 0);
  }
  800% {
    -webkit-transform: translate3d(0, -50px, 0);
            transform: translate3d(0, -50px, 0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate3d(0, -80px, 0);
            transform: translate3d(0, -80px, 0);
    opacity: 0;
  }
}



